@font-face {
  font-family: 'Sora';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/sora/v10/xMQOuFFYT72X5wkB_18qmnndmSdSnn-K.ttf) format('truetype');
}
.app {
  display: flex;
  justify-content: center;
}
.container {
  background: #FAFAFA;
  width: 1100px;
  height: 100vh;
  display: flex;
}
* {
  font-family: 'Sora', sans-serif;
  padding: 0;
  margin: 0;
}
